   
    import React, {useState} from 'react';
    import {Input} from '../../css/style.js';
    import * as actions from '../../actions';
    import BeautyText from "../PRINT/BeautyText";
    import {connect} from 'react-redux';
    import { Link } from 'react-router-dom';
    import {
        GlobalStyle,Container,Grid,AreaBox,Text,
        TextBox,Button,ParamBox,naked,link
    } from '../../css/style.js'

    const FormOneSimp =
    ({formName,ownerId,agentId,state,
      onOwnerDicCreate,onOwnerDicUpdate,
      onAgentDicCreate,onAgentDicUpdate}) => {
        console.log("formName in FormOneSimp :", formName);

    // state,formName,ownerId,agentId    
    const actorId = 
    formName => {
        switch(formName){
            case "ownerDic": return ownerId;
            case "agentDic": return agentId;
            default: return "no actor id";
            };
            };
    const importData = 
    (formName, actorId) => ({
        actorData:
            state[formName]
            .filter(e => e.id===actorId)[0],
        actorName: 
            state[formName]
            .map(actor => actor.name),
        initStateForNewActor:
            state.home.initStateForNewActor("YL")
        });
    const currentImportData = 
    () => (
        importData(
            formName,
            actorId(formName))
            );
    const [formData, setFormData] = useState(
        {...(actorId(formName) === undefined 
            ? {...currentImportData().initStateForNewActor} 
            : currentImportData().actorData)
            }     
            );
    const updateFormData = 
    event => {
        setFormData({
            ...formData,
            [event.target.name]: event.target.value
            });
            };
    const handleSubmit = 
    e => {
        e.preventDefault();
        if(formName==="ownerDic"){
            if(ownerId === undefined){ 
                onOwnerDicCreate(formData)
                }else{
                onOwnerDicUpdate(formData)
                }
                }
        else{
        if(formName==="agentDic"){
             if(agentId === undefined){
                onAgentDicCreate(formData)
                }else{
                onAgentDicUpdate(formData)
                }
                }
                }              
                };
    const Id = 
    () => {
        console.log("formData in formDog: ", formData.id);
        return (typeof formData.id==="number" 
            ? formData.id 
            : state[formName][state[formName].length-1].id+1
            );  
            };
    const Name = 
    () => {
       // console.log("state[formName]:", state[formName]);
        console.log("fprmData: ", formData);
        return (formData.name==="" 
            ? formData.compShortName
            : state[formName]
              .filter(e => e.name===formData.name)[0].name
        ); 
    };
      const handleSaveCountedData =
      ()=> 
          setFormData({...formData,
            id:Id(),
            name:Name()
        });
    const SaveButton = () => {
        return <button onClick={handleSaveCountedData}>Save</button>    
    };

    return (
        <form>
            <br/>
                <label>    : <br/>
                    <Input
                        id="compFullName"
                        value= {formData.compFullName}
                        onChange={e => updateFormData(e)}
                        placeholder="    "
                        type="text"
                        name="compFullName"
                        required
                        maxlength="120"
                        />
                        <br/>
                        </label>
               <label>    : <br/>
                    <Input
                        id="compShortName"
                        value= {formData.compShortName}
                        onChange={e => updateFormData(e)}
                        placeholder="    "
                        type="text"
                        name="compShortName"
                        required
                        maxlength="120"
                        />
                        <br/>
                        </label>
               <label>  : <br/>
                    <Input
                        id="INN"
                        value= {formData.INN}
                        onChange={e => updateFormData(e)}
                        placeholder="  "
                        type="text"
                        name="INN"
                        required
                        minlength="10"
                        maxlength="10"
                    /><br/>
               </label>
               <label>  : <br/>
                    <Input
                        id="KPP"
                        value= {formData.KPP}
                        onChange={e => updateFormData(e)}
                        placeholder="  "
                        type="text"
                        name="KPP"
                        required
                    /><br/>
               </label>
               <label>  : <br/>
                    <Input
                        id="OGRN"
                        value= {formData.OGRN}
                        onChange={e => updateFormData(e)}
                        placeholder="  "
                        type="text"
                        name="OGRN"
                        required
                        minlength="13"
                        maxlength="13"
                    /><br/>
               </label>
               <label>  : <br/>
                    <Input
                        id="OKPO"
                        value= {formData.OKPO}
                        onChange={e => updateFormData(e)}
                        placeholder="  "
                        type="text"
                        name="OKPO"
                        required
                        minlength="8"
                        maxlength="8"
                    /><br/>
               </label>
               <label>    : <br/>
                    <Input
                        id="GosRegDate"
                        value= {formData.GosRegDate}
                        onChange={e => updateFormData(e)}
                        placeholder="    "
                        type="text"
                        name="GosRegDate"
                        required
                    /><br/>
               </label>
               <label>   : <br/>
                    <Input
                        id="YurAdress"
                        value= {formData.YurAdress}
                        onChange={e => updateFormData(e)}
                        placeholder="   "
                        type="text"
                        name="YurAdress"
                        required
                    /><br/>
               </label>
               <label>   : <br/>
                    <Input
                        id="FactAdress"
                        value= {formData.FactAdress}
                        onChange={e => updateFormData(e)}
                        placeholder="   "
                        type="text"
                        name="FactAdress"
                        required
                    /><br/>
               </label>
               <label> .  () : <br/>
                    <Input
                        id="GenDirector"
                        value= {formData.GenDirector}
                        onChange={e => updateFormData(e)}
                        placeholder=" .  () "
                        type="text"
                        name="GenDirector"
                        required
                    /><br/>
               </label>
               <label>  () : <br/>
                    <Input
                        id="Buhgalter"
                        value= {formData.Buhgalter}
                        onChange={e => updateFormData(e)}
                        placeholder="  () "
                        type="text"
                        name="Buhgalter"
                        required
                    /><br/>
               </label>
               <label>  : <br/>
                    <Input
                        id="tel"
                        value= {formData.tel}
                        onChange={e => updateFormData(e)}
                        placeholder="  "
                        type="text"
                        name="tel"
                        required
                    /><br/>
               </label><br/>
           <br/>
             <br/>
           <br/>
               <label>   : <br/>
                   <Input
                       id="bankName"
                       value= {formData.bankName}
                       onChange={e => updateFormData(e)}
                       placeholder="   "
                       type="text"
                       name="bankName"
                       required
                   /><br/>
               </label>
               <label>  : <br/>
                   <Input
                       id="BIK"
                       value= {formData.BIK}
                       onChange={e => updateFormData(e)}
                       placeholder=" BIK "
                       type="text"
                       name="BIK"
                       required
                   /><br/>
               </label>
               <label>   : <br/>
                   <Input
                       id="RS"
                       value= {formData.RS}
                       onChange={e => updateFormData(e)}
                       placeholder="   "
                       type="text"
                       name="RS"
                       required
                   /><br/>
               </label>
               <label> .  : <br/>
                   <Input
                       id="KS"
                       value= {formData.KS}
                       onChange={e => updateFormData(e)}
                       placeholder=" .  "
                       type="text"
                       name="KS"
                       required
                   /><br/>
               </label><br/>

             <button onClick={handleSubmit}>Submit</button>
                <SaveButton/>
             
        </form>
    );
    };
    
    const mapStateToProps = _state => ({
        state: _state    
    });
    const mapDispatchToProps = _dispatch => ({
        onOwnerDicCreate: 
            data => _dispatch(actions.ownerDicCreate(data)),
        onOwnerDicUpdate: 
            data => _dispatch(actions.ownerDicUpdate(data)),
        onAgentDicCreate: 
            data => _dispatch(actions.agentDicCreate(data)),
        onAgentDicUpdate: 
            data => _dispatch(actions.agentDicUpdate(data))
    });

    export default connect (
        mapStateToProps,
        mapDispatchToProps
    )(FormOneSimp);  
    
    //export default FormOneSimp; 

    
